import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { IChoiceWidget, SensorPreset } from '..';

@Component({
  selector: 'poc-choice-widget',
  templateUrl: './choice-widget.component.html',
  styleUrls: ['./choice-widget.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class ChoiceWidgetComponent<TChoice> {

  @Input()
  widgetModel!: IChoiceWidget<SensorPreset>;

  @Output()
  choiceSelected = new EventEmitter<TChoice>();

  _onChange(ev: {originalEvent: Event, value: TChoice}) {
    this.choiceSelected.emit(ev.value);
  }
}
