import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { INumberWidget } from '..';

@Component({
  selector: 'poc-number-widget',
  templateUrl: './number-widget.component.html',
  styleUrls: ['./number-widget.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class NumberWidgetComponent {

  @Input()
  widgetModel!: INumberWidget;

  @Input()
  value$!: Observable<number>;

  @Output()
  numberInput = new EventEmitter<number>();

  _numberInput(ev: {originalEvent: Event, value: number}) {
    this.numberInput.emit(ev.value);
  }

  _onFocus(ev: FocusEvent) {
    (ev.target as HTMLInputElement).select();
  }
}
