import { ChangeDetectionStrategy, Component, HostBinding, Input } from '@angular/core';
import { PanelResizeService } from './panel-resize.service';

@Component({
  selector: 'poc-panel',
  templateUrl: './panel.component.html',
  styleUrls: ['./panel.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PanelComponent {

  @Input()
  label!: string;

  @Input()
  @HostBinding('class.collapsed')
  collapsed = true;

  constructor(private panelResizeService: PanelResizeService) {

  }

  collapsedIconClass = ['pi', 'pi-chevron-right'];
  openedIconClass = ['pi', 'pi-chevron-down'];
  get iconClasses() {
    return this.collapsed ? this.collapsedIconClass : this.openedIconClass;
  }

  _headerClicked(ev: MouseEvent) {
    this.collapsed = !this.collapsed;
    this.panelResizeService.onResize.next();
  }
}
