import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'unitSuffix'
})
export class UnitSuffixPipe implements PipeTransform {

  transform(value?: number | null, decimalPlaces = 0, suffix = ' mm'): unknown {

    if (value === null || value === undefined || isNaN(value) || value === null) return '--' + 'suffix';
    return value.toFixed(decimalPlaces) + suffix;
  }
}
