import { Injectable } from "@angular/core";
import { ComponentStore } from "@ngrx/component-store";
import { combineLatest, Observable } from "rxjs";
import { distinct, map } from "rxjs/operators";

export interface EngineState {

  sensorWidth: number;
  sensorHeight: number;
  sensorDiagonal: number;

  focalLength: number;

  aovWidth: number;
  aovHeight: number;
  aovDiag: number;

  viewWidth: number;
  viewHeight: number;
  viewDistance: number;
}

@Injectable()
export class EngineStateStore extends ComponentStore<EngineState> {

  readonly sensorWidth$: Observable<number> = this.select(state => state.sensorWidth);
  readonly sensorHeight$: Observable<number> = this.select(state => state.sensorHeight);

  readonly sensorDiagonal$: Observable<number> = combineLatest([this.sensorWidth$, this.sensorHeight$]).pipe(map(([sensorWidth, sensorHeight]) => {
    const diag = Math.sqrt((sensorHeight * sensorHeight) + (sensorWidth * sensorWidth));
    return Math.round(diag * 10) / 10;
  })).pipe(distinct());
  readonly sensorArea$: Observable<number> = combineLatest([this.sensorWidth$, this.sensorHeight$]).pipe(map(([sensorWidth, sensorHeight]) => {
    const area = sensorHeight * sensorWidth;
    return Math.round(area * 10) / 10;
  })).pipe(distinct());

  readonly focalLength$: Observable<number> = this.select(state => state.focalLength);

  readonly sensorSize$: Observable<{sensorWidth: number, sensorHeight: number }> = combineLatest([this.sensorWidth$, this.sensorHeight$])
      .pipe(map(([sensorWidth, sensorHeight]) => ({sensorWidth, sensorHeight})));

  readonly viewWidth$: Observable<number> = this.select(state => state.viewWidth);
  readonly viewHeight$: Observable<number> = this.select(state => state.viewHeight);
  readonly viewDistance$: Observable<number> = this.select(state => state.viewDistance);

  readonly viewSize$: Observable<{viewWidth: number, viewHeight: number }> = combineLatest([this.viewWidth$, this.viewHeight$])
  .pipe(map(([viewWidth, viewHeight]) => ({viewWidth, viewHeight})));

  readonly aovWidth$: Observable<number> = this.select(state => state.aovWidth);
  readonly aovHeight$: Observable<number> = this.select(state => state.aovHeight);
  readonly aovDiag$: Observable<number> = this.select(state => state.aovDiag);

  readonly setSensorWidth = this.updater((state, sensorWidth: number) => updateSensorSize(state, sensorWidth, state.sensorHeight));
  readonly setSensorHeight = this.updater((state, sensorHeight: number) => updateSensorSize(state, state.sensorWidth, sensorHeight));

  readonly setSensorSize = this.updater((state, size: { sensorWidth: number, sensorHeight: number }) => updateSensorSize(state, size.sensorWidth, size.sensorHeight));

  readonly setFocalLength = this.updater((state, focalLength: number) => updateFocalLength(state, focalLength));

  readonly setViewDistance = this.updater((state, viewDistance: number) => setViewDistance(state, viewDistance));


  constructor() {
    super({
      focalLength: 1,
      sensorHeight: 1,
      sensorWidth: 1,
      sensorDiagonal: 1,
      viewHeight: 1,
      viewWidth: 1,
      viewDistance: 1,
      aovDiag: 1,
      aovHeight: 1,
      aovWidth: 1
    });
  }
}


function radToDeg(rad: number) {
  return rad * 180 / Math.PI;
}

function degToRad(deg: number) {
  return deg * Math.PI / 180;
}

function updateSensorSize(oldState: EngineState, sensorWidth: number, sensorHeight: number): EngineState {

  const { aovHeight, aovWidth } = updateAngleOfView(sensorWidth, sensorHeight, oldState.focalLength);
  const { viewWidth, viewHeight } = updateViewSizeForDistance(oldState.viewDistance, aovWidth, aovHeight);

    return {
      ...oldState,
      sensorWidth,
      sensorHeight,
      aovWidth,
      aovHeight,
      viewWidth,
      viewHeight
    };
}

function updateFocalLength(oldState: EngineState, focalLength: number): EngineState {

  if (isNaN(focalLength) || focalLength === null || focalLength === 0) {
    return oldState;
  }

  const { aovHeight, aovWidth } = updateAngleOfView(oldState.sensorWidth, oldState.sensorHeight, focalLength);
  const { viewHeight, viewWidth } = updateViewSizeForDistance(oldState.viewDistance, aovWidth, aovHeight);

  return {
    ...oldState,
    focalLength,
    aovWidth,
    aovHeight,
    viewWidth,
    viewHeight
  };
}

function setViewDistance(oldState: EngineState, viewDistance: number) {

  viewDistance = Math.round(viewDistance * 10) / 10;

  const { viewHeight, viewWidth } = updateViewSizeForDistance(viewDistance, oldState.aovWidth, oldState.aovHeight);

  return {
    ...oldState,
    viewDistance,
    viewWidth,
    viewHeight
  };
}

function updateAngleOfView(sensorWidth: number, sensorHeight: number, focalLength: number) {
  return {
    aovHeight: calculateAngleOfView(sensorHeight, focalLength),
    aovWidth: calculateAngleOfView(sensorWidth, focalLength)
  };
}

function calculateAngleOfView(sensorDimension: number, focalLength: number) {
  return 2 * Math.atan(sensorDimension / (2 * focalLength));
}

function updateViewSizeForDistance(viewDistance: number, aovWidth: number, aovHeight: number) {
  return {
    viewWidth: calculateViewSizeForDistance(viewDistance, aovWidth),
    viewHeight: calculateViewSizeForDistance(viewDistance, aovHeight)
  };
}

function calculateViewSizeForDistance(distance: number, FOV: number) {
  const viewSize = 2 * distance * Math.tan(FOV / 2);
  return Math.round(viewSize * 10) / 10
}
