import { ChangeDetectionStrategy, Component, HostBinding, Input } from '@angular/core';

@Component({
  selector: 'poc-collapsable-widget',
  templateUrl: './collapsable-widget.component.html',
  styleUrls: ['./collapsable-widget.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class CollapsableWidgetComponent {

  @Input()
  label = '';

  @Input()
  value: any;

  @Input()
  @HostBinding('class.collapsed')
  collapsed = true;

  collapsedIconClass = ['pi', 'pi-chevron-right'];
  openedIconClass = ['pi', 'pi-chevron-down'];
  get iconClasses() {
    return this.collapsed ? this.collapsedIconClass : this.openedIconClass;
  }
}
