import {Component, ElementRef, AfterViewInit, ViewChild, NgZone, Input, HostBinding} from '@angular/core';
import { asyncScheduler, interval, Observable } from 'rxjs';
import {EngineService} from './engine.service';
import { createCameraEngineWidget } from './camera-engine.widget';
import { EngineStateStore, EngineState } from './store/engine.store';
import { ControllerStateStore } from './store/controller.store';
import { delay, filter, switchMap, takeWhile, tap, throttleTime, withLatestFrom } from 'rxjs/operators';
import { PanelResizeService } from '../../core/components/panel/panel-resize.service';

@Component({
  selector: 'poc-engine',
  templateUrl: './engine.component.html',
  styleUrls: ['./engine.component.scss'],
  providers: [ControllerStateStore]
})
export class EngineComponent implements AfterViewInit {

  @ViewChild('cameraView', {static: true})
  public cameraView!: ElementRef<HTMLDivElement>;

  @ViewChild('overView', {static: true})
  public overView!: ElementRef<HTMLDivElement>;

  @Input()
  @HostBinding('class.display-camera-view')
  displayCameraView = true;

  cameraEngineState$: Observable<EngineState>;

  cameraEngineWidget = createCameraEngineWidget(this.store$);

  public constructor(
    private engServ: EngineService,
    private store$: EngineStateStore,
    private controllerStore$: ControllerStateStore,
    private panelResizeService: PanelResizeService) {

    this.cameraEngineState$ = this.store$.select(x => x);

    this.controllerStore$.up$.pipe(filter(x => x)).pipe(withLatestFrom(this.store$.viewDistance$)).pipe(switchMap(([pressed, currentDistance]) => {
      this.store$.setViewDistance(currentDistance + 1);
      return interval(50, asyncScheduler).pipe(delay(500, asyncScheduler)).pipe(withLatestFrom(this.controllerStore$.up$, this.store$.viewDistance$)).pipe(tap(([i, isDown, currentDistance]) => {
        const dif = Math.min(Math.ceil(i / 10), 10);
         this.store$.setViewDistance(currentDistance + dif);
      })).pipe(takeWhile(([i, isPressed, curr]) => isPressed ));
    })).subscribe();

    this.controllerStore$.down$.pipe(filter(x => x)).pipe(withLatestFrom(this.store$.viewDistance$)).pipe(switchMap(([pressed, currentDistance]) => {
      this.store$.setViewDistance(currentDistance - 1);
       return interval(50, asyncScheduler).pipe(delay(500, asyncScheduler)).pipe(withLatestFrom(this.controllerStore$.down$, this.store$.viewDistance$)).pipe(tap(([i, isDown, currentDistance]) => {
          const dif = Math.min(Math.ceil(i / 10), 10);
          this.store$.setViewDistance(currentDistance - dif);
      })).pipe(takeWhile(([i, isPressed, curr]) => isPressed ));
    })).subscribe();

    // this.controllerStore$.leftJoyY$.pipe(filter(x => Math.abs(x) > .01)).pipe(throttleTime(100, asyncScheduler, {trailing: true})).pipe(withLatestFrom(this.store$.viewDistance$)).subscribe(([leftJoyY, currentDistance]) => {
    //   this.store$.setViewDistance(currentDistance - Math.ceil(leftJoyY * 10));
    // });

    this.controllerStore$.rightJoyY$.pipe(filter(x => Math.abs(x) > .07)).pipe(throttleTime(50, asyncScheduler, {trailing: true, leading: true})).pipe(withLatestFrom(this.store$.viewDistance$)).pipe(switchMap(([value, currentDistance]) => {
      this.store$.setViewDistance(currentDistance - Math.ceil(value * 20));
      return interval(50, asyncScheduler).pipe(withLatestFrom(this.controllerStore$.rightJoyY$, this.store$.viewDistance$)).pipe(tap(([i, joyY, currentDistance]) => {
        this.store$.setViewDistance(currentDistance - Math.ceil(joyY * 20));
      })).pipe(takeWhile(([i, joyY, curr]) => Math.abs(joyY) > .07 ));
    })).subscribe();

    this.panelResizeService.onResize$.subscribe(() => {
      const { clientWidth, clientHeight } = this.overView.nativeElement;
      this.engServ.setSize(clientWidth, clientHeight);
    });

    window.addEventListener("gamepadconnected", (event) => {
      console.log("A gamepad connected:");
      console.log(event.gamepad);

      (event.gamepad as any).vibrationActuator.playEffect("dual-rumble", {
        startDelay: 0,
        duration: 100,
        weakMagnitude: .4,
        strongMagnitude: .8
      });

      this.pollGamepad();
    });

    window.addEventListener("gamepaddisconnected", (event) => {
      console.log("A gamepad disconnected:");
      console.log(event.gamepad);
    });

  }

  pollGamepad() {
    const gamepads = navigator.getGamepads();
    if (gamepads.length > 0) {

      const gamepad = gamepads[0];
      if (gamepad) this.controllerStore$.setControllerState(gamepad);

      requestAnimationFrame(() => {
        //this.ngZone.runOutsideAngular(() => {
          this.pollGamepad();
        //});
      });
    }
  }

  public ngAfterViewInit(): void {

    this.engServ.createScene(this.overView, this.cameraView);
    this.engServ.animate();

    setTimeout(() => {
      this.store$.setSensorSize({sensorWidth: 5.33, sensorHeight: 4});
      this.store$.setFocalLength(6);
      this.store$.setViewDistance(200);
    }, 1000);
  }
}
