import { AfterViewInit } from '@angular/core';
import { ChangeDetectionStrategy, Component } from '@angular/core';
import { PanelResizeService } from './core/components/panel/panel-resize.service';
import { IRangeWidget } from './core/widgets';
import { round } from './features/engine/camera-engine.widget';
import { EngineStateStore } from './features/engine/store/engine.store';

@Component({
  selector: 'poc-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush,
  providers: [PanelResizeService]
})
export class AppComponent implements AfterViewInit {
  title = 'coretainium-optics';

  cameraHeightWidget!: IRangeWidget;

  displayCameraOverview = false;

  constructor(
    private panelResizeService: PanelResizeService,
    public store$: EngineStateStore
  ) {

    this.panelResizeService.onResize$.subscribe(() => {
      console.log('resize');
    });

    this.cameraHeightWidget = {
      min: 200,
      max: 1300,
      step: 1,
      value$: this.store$.viewDistance$,
      label: 'Camera Height',
      readonly: false,
      setValue: (newPosition: number) => this.store$.setViewDistance(round(newPosition, 1)),
      type: 'range'
    }
  }

  resizeEnd(ev: Event) {
    console.log(ev);
    this.panelResizeService.onResize.next();
  }

  ngAfterViewInit() {
    setTimeout(() => {
      this.panelResizeService.onResize.next();
    }, 150);
  }
}
