import { EventEmitter, HostBinding, HostListener } from '@angular/core';
import { ChangeDetectionStrategy, Component, Input, Output } from '@angular/core';

@Component({
  selector: 'poc-pinned-button',
  templateUrl: './pinned-button.component.html',
  styleUrls: ['./pinned-button.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class PinnedButtonComponent {

  @Input()
  @HostBinding('class.checked')
  checked = false;

  @Output()
  toggled = new EventEmitter<boolean>();

  @HostListener('click')
  clicked() {
    this.checked = !this.checked;
    this.toggled.emit(this.checked);
  }

}
