import { ChangeDetectionStrategy, Component, EventEmitter, Input, Output } from '@angular/core';
import { Observable } from 'rxjs';
import { IRangeWidget } from '..';

@Component({
  selector: 'poc-range-widget',
  templateUrl: './range-widget.component.html',
  styleUrls: ['./range-widget.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class RangeWidgetComponent {

  @Input()
  widgetModel!: IRangeWidget;

  @Input()
  value$!: Observable<number>;

  @Output()
  rangeInput = new EventEmitter<number>();

  _rangeInput(ev: {originalEvent: Event, value: number}) {
    this.rangeInput.emit(ev.value);
  }

  _onFocus(ev: FocusEvent) {
    (ev.target as HTMLInputElement).select();
  }
}
