import { Injectable } from '@angular/core';
import { ComponentStore } from '@ngrx/component-store';
import { Observable, ReplaySubject } from 'rxjs';
import { take } from 'rxjs/operators';

export interface IFocalLengthPreset {
  id: string;
  sensorWidth: number;
  sensorHeight: number;
  focalLength: number;
  viewDistance: number;
  viewWidth: number;
  viewHeight: number;
}

@Injectable({
  providedIn: 'root'
})
export class CameraPresetService {


  presets$ = this.presetsStore.presets$;

  private _localStoreKey = 'focal-presets';

  constructor(private presetsStore: PresetsStore) {

    const presetString = localStorage.getItem(this._localStoreKey);

    try {
      const presets = JSON.parse(presetString ?? '[]');
      this.presetsStore.initPresets(presets);
    } catch (e) {
      console.log('Unable to parse presets');
    }
  }

  addPreset(newPreset: IFocalLengthPreset) {
    this.presetsStore.addPreset(newPreset);
    this.presets$.pipe(take(1)).subscribe(presets => {
      localStorage.setItem(this._localStoreKey, JSON.stringify(presets));
    });
  }

  removePreset(preset: IFocalLengthPreset) {
    this.presetsStore.removePreset(preset);
    this.presets$.pipe(take(1)).subscribe(presets => {
      localStorage.setItem(this._localStoreKey, JSON.stringify(presets));
    });
  }
}




export interface PresetsState {
  presets: IFocalLengthPreset[]
}

@Injectable()
export class PresetsStore extends ComponentStore<PresetsState> {

  readonly presets$: Observable<IFocalLengthPreset[]> = this.select(state => state.presets);

  readonly initPresets = this.updater((state, presets: IFocalLengthPreset[]) => {
    return {
      ...state,
      presets
    }
  });

  readonly addPreset = this.updater((state, newPreset: IFocalLengthPreset) => {
    const presets = [...state.presets, newPreset];
    return {
      ...state,
      presets
    }
  });

  readonly removePreset = this.updater((state, preset: IFocalLengthPreset) => {
    const presets = state.presets.filter(x => x.id !== preset.id);

    return {
      ...state,
      presets
    }
  });

  constructor() {
    super({
      presets: []
    });
  }
}
