import { Injectable } from "@angular/core";
import { ComponentStore } from "@ngrx/component-store";
import { combineLatest, Observable } from "rxjs";
import { map } from "rxjs/operators";

export interface ControllerState {

  up: boolean;
  down: boolean;
  left: boolean;
  right: boolean;

  button1: boolean;

  leftJoyX: number;
  leftJoyY: number;

  rightJoyX: number;
  rightJoyY: number;
}

@Injectable()
export class ControllerStateStore extends ComponentStore<ControllerState> {

  readonly up$: Observable<boolean> = this.select(state => state.up);
  readonly down$: Observable<boolean> = this.select(state => state.down);
  readonly left$: Observable<boolean> = this.select(state => state.left);
  readonly right$: Observable<boolean> = this.select(state => state.right);

  readonly button1$: Observable<boolean> = this.select(state => state.button1);

  readonly leftJoyX$: Observable<number> = this.select(state => state.leftJoyX);
  readonly leftJoyY$: Observable<number> = this.select(state => state.leftJoyY);

  readonly rightJoyX$: Observable<number> = this.select(state => state.rightJoyX);
  readonly rightJoyY$: Observable<number> = this.select(state => state.rightJoyY);

  readonly setControllerState = this.updater((state, gamepad: Gamepad) => setControllerState(state, gamepad));

  constructor() {
    super({
      button1: false,
      down: false,
      left: false,
      leftJoyX: 0,
      leftJoyY: 0,
      right: false,
      rightJoyX: 0,
      rightJoyY: 0,
      up: false
    });
  }
}

function setControllerState(oldState: ControllerState, gamepad: Gamepad): ControllerState {

  return {
    button1: gamepad.buttons[0].pressed,
    down: gamepad.buttons[13].pressed,
    left: gamepad.buttons[14].pressed,
    leftJoyX: gamepad.axes[0],
    leftJoyY: gamepad.axes[1],
    right: gamepad.buttons[5].pressed,
    rightJoyX: gamepad.axes[2],
    rightJoyY: gamepad.axes[3],
    up: gamepad.buttons[12].pressed
  };
}
