import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'apertureAbbreviation'
})
export class ApertureAbbreviationPipe implements PipeTransform {

  transform(value: string): string {
    switch (value) {
      case 'Aperture Priority':
        return 'A';
      case 'Shutter Priority':
        return 'S';
      case 'Program Auto':
        return 'P';
      case 'Auto':
        return 'AUTO';
      case 'Movie (Manual)':
        return 'Movie';
      case 'Manual':
        return 'M';
      default:
        return 'X';
    };
  }

}
