
import { CameraHelper, PerspectiveCamera, MathUtils, LineBasicMaterial } from 'three';
import { EngineStateStore } from './store/engine.store';

export class CameraEngineService {

  public viewCamera!: THREE.PerspectiveCamera;
  public _viewCamera!: THREE.PerspectiveCamera;
  public viewCameraHelper!: THREE.CameraHelper;

  public lensCamera!: THREE.PerspectiveCamera;
  public lensCameraHelper!: THREE.CameraHelper;

  constructor(
    private store$: EngineStateStore
  ) {

    this.store$.select(x => x).subscribe(newState => {

      //if (newState.aovHeight === 0) return;

      const angleOfView = MathUtils.radToDeg(newState.aovHeight)
      const aspectRatio = newState.sensorWidth / newState.sensorHeight;

      if (!this.viewCamera) {

        this.viewCamera = new PerspectiveCamera(angleOfView, aspectRatio, .1, 10000);
        this._viewCamera = new PerspectiveCamera(angleOfView, aspectRatio, 25, newState.viewDistance);
        this.viewCamera.fov = newState.focalLength;
        this._viewCamera.fov = newState.focalLength;
        this.viewCamera.position.set(0,0,0);
        this._viewCamera.position.set(0,0,0);
        this.viewCamera.rotateX(MathUtils.degToRad(-90));
        this._viewCamera.rotateX(MathUtils.degToRad(-90));

        this.viewCameraHelper = new CameraHelper(this._viewCamera);
        this.viewCameraHelper.material = new LineBasicMaterial({
          color: 'lime',
          linewidth: 300
        });

        this.viewCameraHelper.frustumCulled = false;

        this.lensCamera = new PerspectiveCamera(angleOfView, aspectRatio, newState.focalLength / 2, newState.focalLength);
        this.lensCamera.fov = newState.focalLength;
        this.lensCamera.position.set(0,0,0);
        this.lensCamera.rotateX(MathUtils.degToRad(90));
        this.lensCameraHelper = new CameraHelper(this.lensCamera);
      }

      this.viewCamera.fov = angleOfView;
      this._viewCamera.fov = angleOfView;
      this.viewCamera.aspect = aspectRatio;
      this._viewCamera.aspect = aspectRatio;
      this._viewCamera.far = newState.viewDistance;

      this.lensCamera.fov = angleOfView;
      this.lensCamera.aspect = aspectRatio;
      this.lensCamera.near = newState.focalLength / 2;
      this.lensCamera.far = newState.focalLength;

      this.updateCameras();
    });
  }

  setCameraPosition(newPosition: number) {
    this._viewCamera.position.setY(newPosition);
    this.viewCamera.position.setY(newPosition);
    this.lensCamera.position.setY(newPosition);
    this.updateCameras();
  }

  updateCameras() {

    this.lensCamera.updateProjectionMatrix();
    this.lensCamera.updateMatrixWorld(true);
    this.lensCameraHelper.update();
    this.viewCamera.updateProjectionMatrix();
    this.viewCamera.updateMatrixWorld(true);
    this._viewCamera.updateProjectionMatrix();
    this._viewCamera.updateMatrixWorld(true);
    this.viewCameraHelper.update();
  }
}
