import { of } from "rxjs"
import { map } from 'rxjs/operators';
import { IChoiceWidget, ILabelWidget, INumberWidget, IRangeWidget, ISectionWidget, SensorPreset } from "../../core/widgets";
import { EngineStateStore } from "./store/engine.store"

export const createCameraEngineWidget = (store$: EngineStateStore) => {

  const cameraHeightWidget: IRangeWidget = {
    min: 200,
    max: 1300,
    step: 1,
    value$: store$.viewDistance$,
    label: 'Camera Height',
    readonly: false,
    setValue: (newPosition) => store$.setViewDistance(round(newPosition, 1)),
    type: 'range'
  }

  const focalLengthWidget: IRangeWidget = {
    min: 0,
    max: 100,
    step: .1,
    value$: store$.focalLength$,
    label: 'Focal Length',
    readonly: false,
    setValue: (focalLength) => store$.setFocalLength(focalLength),
    type: 'range'
  }

  const mainSection: ISectionWidget = {
    type: 'section',
    label: 'Camera Optical Settings',
    children: [
      cameraHeightWidget,
      focalLengthWidget,
      createSensorSizeSection(store$),
      createViewableAreaSection(store$)
    ]
  }

  return mainSection;
}

const createSensorSizeSection = (store$: EngineStateStore) => {

  const sensorWidthWidget: INumberWidget = {
    min: 0,
    max: 1000,
    step: .01,
    value$: store$.sensorWidth$,
    label: 'Sensor Width',
    readonly: false,
    setValue: (sensorX) => store$.setSensorWidth(sensorX),
    type: 'number'
  }
  const sensorHeightWidget: INumberWidget = {
    min: 0,
    max: 1000,
    step: .01,
    value$: store$.sensorHeight$,
    label: 'Sensor Height',
    readonly: false,
    setValue: (sensorY) => store$.setSensorHeight(sensorY),
    type: 'number'
  }

  const sensorPresetWidget: IChoiceWidget<SensorPreset> = {

    type: 'choice',
    label: 'Sensor Presets',
    value$: of(),
    readonly: false,
    choices: [{
      label: '1/2.7" - 5.33mm * 4mm',
      sensorX: 5.33,
      sensorY: 4
    },{
      label: '1/3" - 4.8mm * 3.6mm',
      sensorX: 4.8,
      sensorY: 3.6
    },{
      label: 'Sony a7R III 35mm',
      sensorX: 35.9,
      sensorY: 24
    }],
    setValue: ({sensorX, sensorY}) => {
      store$.setSensorWidth(sensorX);
      store$.setSensorHeight(sensorY);
    }
  }

  const mainSection: ISectionWidget = {
    type: 'section',
    label: 'Sensor Settings',
    children: [
      sensorWidthWidget,
      sensorHeightWidget,
      sensorPresetWidget
    ]
  }

  return mainSection;
}

const createViewableAreaSection = (store$: EngineStateStore) => {

  const viewWidth: ILabelWidget = {
    value$: store$.viewWidth$.pipe(map(value => { return `${round(value, 10)}mm` })),
    readonly: true,
    setValue: (newVal) => newVal,
    label: 'View Width',
    type: 'label'
  }

  const viewHeight: ILabelWidget = {
    value$: store$.viewHeight$.pipe(map(value => { return `${round(value, 10)}mm` })),
    readonly: true,
    setValue: (newVal) => newVal,
    label: 'View Height',
    type: 'label'
  }

  const mainSection: ISectionWidget = {
    type: 'section',
    label: 'View Area',
    children: [
      viewWidth,
      viewHeight
    ]
  }

  return mainSection;
}

export function round(value: number, decimals: number) {
  return Math.round(value * decimals) / decimals;
}
