import { Component, ViewChild } from '@angular/core';
import { take } from 'rxjs/operators';
import { EngineStateStore } from '../../features/engine/store/engine.store';
import { CameraPresetService, IFocalLengthPreset } from '../../services/camera-preset.service';
import { v4 as uuid } from 'uuid';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'poc-lens-preset-table',
  templateUrl: './lens-preset-table.component.html',
  styleUrls: ['./lens-preset-table.component.scss']
})
export class LensPresetTableComponent {

  @ViewChild('main')
  mainRef!: HTMLElement;

  presets$ = this.presetService.presets$;

  cols = [
    { field: 'sensorWidth', header: 'Sensor width' },
    { field: 'sensorHeight', header: 'Sensor Height' },
    { field: 'focalLength', header: 'Focal Length' },
    { field: 'viewDistance', header: 'View Distance' },
    { field: 'viewWidth', header: 'View Width' },
    { field: 'viewHeight', header: 'View Height' }
  ];

  constructor(
    private presetService: CameraPresetService,
    private engineState: EngineStateStore,
    private confirmationService: ConfirmationService) { }

  addPreset() {
    this.engineState.state$.pipe(take(1)).subscribe(x => {

      const {
        focalLength, sensorWidth, sensorHeight, viewWidth, viewHeight, viewDistance
      } = x;

      this.presetService.addPreset({
        id: uuid(),
        focalLength, sensorWidth, sensorHeight, viewWidth, viewHeight, viewDistance
      })
    })
  }

  removePreset(event: MouseEvent, preset: IFocalLengthPreset) {
    this.confirmationService.confirm({
      target: event.target ?? undefined,
      message: 'Do you want to remove this preset?',
      icon: 'pi pi-exclamation-triangle',
      accept: () => {
        this.presetService.removePreset(preset);
      },
      reject: () => {
          //reject action
      }
    });
  }

  recallPreset(preset: IFocalLengthPreset) {

    const { focalLength, viewDistance, sensorHeight, sensorWidth } = preset;
    this.engineState.setSensorSize({ sensorWidth, sensorHeight });
    this.engineState.setFocalLength(focalLength);
    this.engineState.setViewDistance(viewDistance);
  }
}
