import { Injectable } from '@angular/core';
import { asyncScheduler, fromEvent, merge, Observable, ReplaySubject } from 'rxjs';
import { throttleTime } from 'rxjs/operators';

@Injectable()
export class PanelResizeService {

  onResize = new ReplaySubject<any>();
  onResize$: Observable<Event | any>;

  constructor() {
    this.onResize$ = merge(
      fromEvent(window, 'resize'),
      this.onResize.asObservable()
    ).pipe(throttleTime(50, asyncScheduler, {leading: true, trailing: true }));
  }
}
